body {
  display: flex;
  justify-content: center;
  background-color: rgb(145, 191, 221);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width: 48vw;
  height: 100vh;
  overflow: hidden;
}

/* button {
  font-size: 2vh;
}

button:hover {
  
} */

.order-2 {
  order: 2;
}

.align-left {
  align-self: flex-start;
}

.justify-right {
  justify-content: flex-end !important;
}

.align-right {
  align-self: flex-end;
}
.Header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-basis: 6%;
    background-color: rgb(0, 89, 172);
    color: white;
    font-size: 2vh;
}

.Header-userName {
    margin-left: 40px;
}

.Header-button {
    font-size: 1.7vh;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    right: 1em;
}

.Header-button_text {
    margin-right: 5px;
}

.Header-button:hover {
    color: rgb(182, 182, 182);
}
.AuthDialog {
    z-index: 3;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%; 
    height: 100%;
    color: black;
    background-color: rgb(39, 39, 39);
    transition: height 1s; 
}

.AuthDialog-form {
    background-color: rgb(71, 114, 134);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 25vh;
    height: 22vh;
    padding: 5vh;
}

.AuthDialog-input {
    font-size: 1.9vh;
    text-align: center;
}

.AuthDialog-button {
    font-size: 2vh;
}

.AuthDialog-button:hover {
    
}
.Footer {
    flex-basis: 14%;
    border-top: 2px solid grey;
    background-color: rgb(194, 208, 214);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.Footer-textarea-container {
    flex-basis: 85%;
    height: 90%;
}

.Footer-textarea {
    width: 100%;
    height: 100%;
    resize: none;
}

.Footer-button {
    position: relative;
    right: 1.5vw;
    border-radius: 100%;
    border: 3px solid black;
    padding: 1vh;
}

.Footer-button-icon {
    font-size: 3vh
}

.Footer-gap {
    flex-basis: 5%;
}


.Footer-button:hover {
    background-color: rgb(117, 144, 231);
    cursor: pointer;
}
.ChatMessage {
    width: 100%;
    margin-top: 0.75vh;
    margin-bottom: 1.2vh;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}

.ChatMessage-message-avatar {
    width: 9vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.ChatMessage-message-avatar__icon {
    font-size: 3.1vh;
}

.ChatMessage-message-avatar__username {
    font-size: 1.2vh;
    word-break: break-word;
}

.ChatMessage-message-text {
    word-break: break-word;
    flex-basis: 25%;
    background-color: rgb(183, 213, 233);
    border-radius: 3%;
    padding: .7vh 2vh .7vh 1vh;
}
.ChatSpace {
    flex-basis: 80%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: stretch; 
}

.ChatSpace-messages {
    overflow-y: scroll; 
}

::-webkit-scrollbar {
    width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555; 
}
.App {
  /* position: relative; */
  display: flex;
  font-family: 'Roboto', sans-serif;
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: stretch;
  background-color: rgb(255, 255, 255);
}

